      h2 2.4. Estrategias pedagógicas aplicadas al turismo

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Las estrategias pedagógicas aplicadas al turismo se orientan al desarrollo integral de competencias que permitan al guía o intérprete turístico promover experiencias significativas, seguras y sostenibles en los visitantes. Estas estrategias se centran en el aprendizaje vivencial, la participación activa y la interpretación del patrimonio, elementos esenciales para generar un vínculo entre el turista y el entorno.
      p De acuerdo con la Organización Mundial del Turismo (OMT, 2019), la educación en turismo debe integrar principios de sostenibilidad, inclusión cultural y respeto por el entorno natural, fomentando una comprensión más profunda de los destinos visitados. En este sentido, las estrategias pedagógicas buscan no solo transmitir información, sino también formar actitudes responsables y conscientes.
      p En el turismo contemporáneo, especialmente en contextos étnicos y sostenibles, estas estrategias cumplen un papel esencial para transformar las experiencias turísticas en espacios de aprendizaje significativo, respeto cultural y conciencia ambiental. Dichas metodologías permiten que el visitante no solo observe, sino que experimente, sienta y comprenda el valor integral del territorio y de sus comunidades anfitrionas.
      p Entre las estrategias más relevantes se destacan:
    
    .row.justify-content-center.letra-acordeon-titulos
      .col-lg-12
        TabsB.letra-acordeon-titulos(data-aos="fade-left")
          .py-1(titulo="Aprendizaje experiencial" :icono="require('@/assets/curso/temas/tema2/tema5/rec18-tab1.svg')")
            .row
              .col-md-1
              .col-md-7
                .color-adicional-4 Esta metodología se fundamenta en la vivencia directa del entorno y en la reflexión derivada de la experiencia. Los turistas aprenden al interactuar con la naturaleza, las comunidades locales y los recursos culturales del destino. El aprendizaje experiencial convierte cada actividad turística en un proceso activo de descubrimiento, donde el turista se transforma en participante y aprendiz del territorio
                p.color-adicional-4 Por ejemplo, una caminata interpretativa por los senderos del Parque Nacional Natural Tayrona permite al visitante observar la biodiversidad, identificar especies nativas y reflexionar sobre la relación espiritual que los pueblos indígenas mantienen con la Sierra Nevada. 

              .col-md-3
                figure
                  img(src='@/assets/curso/temas/tema2/tema5/rec18-img1.svg', alt='Texto que describa la imagen')
              .col-md-1

          .py-1(titulo="Interpretación del patrimonio" :icono="require('@/assets/curso/temas/tema2/tema5/rec18-tab2.svg')")
            .row
              .col-md-1
              .col-md-7
                p.color-adicional-4 Según Tilden (2007), la interpretación es “una actividad educativa que revela significados e interrelaciones a través del uso de objetos originales, experiencias de primera mano y medios ilustrativos”. Esta técnica trasciende la simple transmisión de datos: busca revelar el alma del lugar, conectando emocional y cognitivamente al visitante con el patrimonio natural o cultural. 
                p.color-adicional-4 En contextos comunitarios, la interpretación del patrimonio puede incluir relatos orales, demostraciones artesanales o rituales tradicionales, que fortalecen el sentido de pertenencia y respeto mutuo. Por ejemplo, una visita guiada a un resguardo indígena donde se explica el significado espiritual del tejido o el uso de plantas medicinales.
            
              .col-md-3
                figure
                  img(src='@/assets/curso/temas/tema2/tema5/rec18-img2.svg', alt='Texto que describa la imagen')
              .col-md-1
          
          .py-1(titulo="Aprendizaje colaborativo" :icono="require('@/assets/curso/temas/tema2/tema5/rec18-tab3.svg')")
            .row
              .col-md-1
              .col-md-7
                .color-adicional-4 El aprendizaje colaborativo se centra en la interacción y cooperación entre los miembros del grupo. A través de actividades conjuntas, los turistas construyen conocimiento compartido, fortaleciendo la comunicación, el respeto y la empatía intercultural. Esta estrategia refuerza el sentido de colectividad y la comprensión de que el turismo responsable es una acción compartida.
                p.color-adicional-4 Por ejemplo, durante una jornada de reforestación en el Eje Cafetero, los visitantes y la comunidad local pueden trabajar en equipos para sembrar árboles nativos, intercambiando saberes sobre agricultura, biodiversidad y prácticas sostenibles.
                                                                
              .col-md-3
                figure
                  img(src='@/assets/curso/temas/tema2/tema5/rec18-img3.svg', alt='Texto que describa la imagen')
          
          .py-1(titulo="Relato interpretativo o storytelling" :icono="require('@/assets/curso/temas/tema2/tema5/rec18-tab4.svg')")
            .row
              .col-md-1
              .col-md-7
                .color-adicional-4 El storytelling utiliza el arte de contar historias para transmitir conocimientos culturales, históricos o ambientales de una forma emocionalmente significativa.
                p.color-adicional-4 Un guía que narra las leyendas del río Amazonas o las historias míticas de la comunidad Wayuu logra que los visitantes conecten la información con la emoción y la memoria. El relato interpretativo humaniza la experiencia turística y permite aprender desde la emoción, generando recuerdos duraderos y respeto profundo por la cultura anfitriona.
                                                              
              .col-md-3
