    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p En conjunto, estas estrategias pedagógicas permiten transformar una visita convencional en una experiencia educativa y transformadora, donde el turista no solo observa, sino que comprende, valora y se compromete con la preservación del patrimonio natural y cultural.
      p En un informe reciente, el Ministerio de Comercio, Industria y Turismo de Colombia (MINCIT) destaca que el país se consolida como referente latinoamericano en turismo indígena con enfoque intercultural y orientado al buen vivir. Esta afirmación se basa en iniciativas comunitarias lideradas por pueblos indígenas, que preservan saberes ancestrales y participan activamente en la gestión turística, generando valor cultural y económico en sus territorios. A continuación se expone un caso de éxito:

    br
    .row.justify-content-center(data-aos="fade-left")
      .col-lg-9
        figure
          img(src='@/assets/curso/temas/tema2/tema4/tema24-figura7.svg', alt='Texto que describa la imagen')

    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Ahora, es importante comprender cuáles son los elementos que amplifican el éxito de estas experiencias:
      .col-md-11
        p Las comunidades han recuperado el control sobre las experiencias turísticas que ofrecen (alojamiento, alimentación, interpretación cultural, rutas y servicios). Esto fortalece su autonomía y la sostenibilidad del proyecto.
        p Se ha incorporado un enfoque intercultural que respeta los valores, la identidad y los conocimientos ancestrales, aumentando la autenticidad de la experiencia y el valor del destino.
        p La articulación entre comunidad, operadores turísticos y entidades gubernamentales permite una mejor estructuración de servicios, marketing y estándares de calidad.

    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La aplicación pedagógica se basa en aquellos aspectos que permiten extraer aprendizajes aplicables a la operación de servicios turísticos, tal como son:
      .col-md-11
        p Responsabilidad social: el turismo comunitario debe empoderar a la comunidad anfitriona, no solo ofrecer recorridos.
        p Diferenciación del servicio: el guía y la operación turística deben adaptarse al contexto cultural, comunicativo y logístico de la comunidad.
        p Sostenibilidad integral: al combinar desarrollo económico local, preservación cultural y conservación ambiental, se consolida un modelo de turismo responsable y transformador.

